import React, {useRef} from 'react';
import {useForm} from "react-hook-form";
import {useDispatch} from "react-redux";
import {addTour} from "../../../store/slices/TourSlice";
import {useNavigate} from "react-router-dom";
import {ToastContainer} from "react-toastify";
import "./Tour.css"

const AddTour = () => {
    const dispatch = useDispatch();
    const {register, handleSubmit, reset} = useForm();
    const navigate = useNavigate();
    const video = useRef(null);
    const image = useRef(null);
    const submit = (data) => {
        if (data) {
            console.log(data);
            const formData = new FormData();
            formData.append('images', data.photo[0]);
            formData.append('title', data.title);
            formData.append('video', data.video[0]);
            formData.append("description", data.description);
            formData.append("description1", data.description1);
            formData.append("price", data.price);
            formData.append("tourDay", data.day);
            formData.append("info", data.info);
            dispatch(addTour(formData));
            reset({
                images: "",
                title: "",
                video: "",
                description: "",
                description1: "",
                price: "",
                day: "",
                info: "",
            })
            if (image.current) {
                image.current.value = ""
            }
            if (video.current) {
                video.current.value = ""
            }
        }
    }
    return (
        <div>
            <form onSubmit={handleSubmit(submit)}>
                <div className={"ee"}>
                    <div className="d-flex flex-column">
                        <label htmlFor="photo">Photo:</label>
                        <input ref={image} style={{borderColor: "coral"}} {...register("photo", {required: true})}
                               type="file" id="photo"/>
                    </div>
                    <div className="d-flex flex-column">
                        <label htmlFor="vidoe">Video</label>
                        <input ref={video} style={{borderColor: "coral"}} {...register("video", {required: true})}
                               type="file" id="video"/>
                    </div>
                </div>
                <div className="inp_family mt-5 border p-3 rounded border-1">

                    <div className="">
                        <label htmlFor="">Title:</label>
                        <textarea style={{borderColor: "coral"}} {...register("title", {required: true})}
                                  className={"form-control"}></textarea>
                    </div>
                    <div className="">
                        <label htmlFor="">Description 1:</label>
                        <textarea style={{borderColor: "coral"}} {...register("description", {required: true})}
                                  className={"form-control"}></textarea>
                    </div>
                    <div className="">
                        <label htmlFor="">Description 2:</label>
                        <textarea style={{borderColor: "coral"}} {...register("description1", {required: true})}
                                  className={"form-control"}></textarea>
                    </div>
                    <div className="">
                        <label htmlFor="">Day Count:</label>
                        <input style={{borderColor: "coral"}} {...register("day", {required: true})}
                               className={"form-control"} type="number"/>
                    </div>
                    <div className="">
                        <label htmlFor="">Price:</label>
                        <input style={{borderColor: "coral"}} {...register("price", {required: true})}
                               className={"form-control"} type="number"/>
                    </div>
                </div>

                <div className="add_info mt-5">
                    <label htmlFor="">
                        Additional Information:
                    </label>
                    <textarea style={{borderColor: "coral"}} {...register("info", {required: true})}
                              className={"w-100 form-control"}
                              rows="7"></textarea>
                </div>

                <div className="btn_family w-100">
                    <button className={"btn button mt-3 btn-lg float-end"}>Save</button>
                </div>
            </form>
            <ToastContainer
                autoClose={3000}
            />
        </div>
    );
};

export default AddTour;