import {createBrowserRouter} from "react-router-dom";
import AdminPage from "../adminPage/AdminPage";
import AddTour from "../adminPage/en/Addtour/AddTour";
import Login from "../en/login/Login";
import AvailableTours from "../adminPage/en/AvailableTours/AvailableTours";
import Comments from "../adminPage/en/comments/Comments";
import Enquiries from "../adminPage/en/enquiry/Enquiries";
import TourDays from "../adminPage/en/tourDays/TourDays";
import Home from "../en/HomePage/Home";
import App1 from "../en/HomePage/App1";

const router = createBrowserRouter([
    {
        path:"/admin",
        element:<AdminPage/>,
        children:[
            {
                path:"/admin/tour",
                element:<AddTour/>
            },{
                path:"/admin/available_tours",
                element:<AvailableTours/>
            },{
                path:"/admin/comments",
                element:<Comments/>
            },{
                path:"/admin/enquiries",
                element:<Enquiries/>
            },{
                path:"/admin/tourDays/:id",
                element:<TourDays/>
            },
        ]
    },{
        path:"/login",
        element:<Login/>,
    },{
        path:"/",
        element:<Home/>,
    },{
        path:"/tour/:id",
        element:<App1/>,
    },
]);

export default router