import React, {useState} from 'react';
import './MainContent.css';
import Rodal from "rodal";
import {addEnquiry} from "../../../store/slices/EnquirySlice";
import {useDispatch, useSelector} from "react-redux";
import "rodal//lib/rodal.css"
import { ToastContainer, toast } from 'react-toastify';
import {useNavigate} from "react-router-dom";

const MainContent = () => {
    const visible = useSelector(state => state.enquiry.visible);
    const navigate = useNavigate();
    const dispatch = useDispatch();
    const tour = useSelector((state)=>state.tour)
    const [text, setText] = useState('')
    const [lastName, setLastName] = useState('')
    const [firstName, setFirstName] = useState('')
    const [phone, setPhone] = useState('')
    const [email, setEmail] = useState('')
    const [tourId, setTourId] = useState('')
    const [visible2, setVisible2] = useState(false)
    function sendEnquiry(e) {
        e.preventDefault()
        if(lastName && firstName && phone && email && text && tourId){
            let enquiry= {
                lastName, firstName, phone, email, text, tourId
            }
            dispatch(addEnquiry(enquiry))
            setVisible2(false)
        }else{
            toast.warning("fill the blanks !")
        }
    }

    return (
       <div>
           {
               localStorage.getItem("language")==="LANG1"? <main className="main-content">
                   <div className={"div2"}>
                       <h1 className={"textFont"}>
                           It's Time To Explore
                       </h1>
                       <h1 className={"textFont"}>
                           The World
                       </h1>
                       <button  onClick={()=>{setVisible2(true)}} className="plan-trip-btn">Plan Your Trip</button>
                   </div>
               </main>:localStorage.getItem("language")==="LANG2"? <main className="main-content">
                   <div className={"div2"}>
                       <h1 className={"textFont"}>
                           Il est temps d'explorer
                       </h1>
                       <h1 className={"textFont"}>
                           Le monde
                       </h1>
                       <button  onClick={()=>{setVisible2(true)}} className="plan-trip-btn">Planifiez votre voyage</button>
                   </div>
               </main>:localStorage.getItem("language")==="LANG3"? <main className="main-content">
                   <div className={"div2"}>
                       <h1 className={"textFont"}>
                           Es ist Zeit zu erkunden
                       </h1>
                       <h1 className={"textFont"}>
                           Die Welt
                       </h1>
                       <button   onClick={()=>{setVisible2(true)}} className="plan-trip-btn">Planen Sie Ihre Reise</button>
                   </div>
               </main>:""
           }
           <Rodal width={500} height={300} visible={visible2} onClose={() => {setVisible2(false)}}>
               <form onSubmit={sendEnquiry} className={"form mt-3"}>
                   <div className="d-flex flex-column">
                       <div className="d-flex gap-3">
                           <input onChange={(e)=>setLastName(e.target.value)} type="text" placeholder="Lastname" className={"form-control b"} value={lastName}/>
                           <input onChange={(e)=>setPhone(e.target.value)} type="number" placeholder="Phone" className={"form-control b"} value={phone} maxLength={13}/>
                       </div>
                       <div className="d-flex gap-3">
                           <input onChange={(e)=>setFirstName(e.target.value)} type="text" placeholder="Firstname" className={"form-control b"} value={firstName}/>
                           <input onChange={(e)=>setEmail(e.target.value)} type="email" placeholder="Email" className={"form-control b"} value={email} />
                       </div>
                       <textarea onChange={(e)=>setText(e.target.value)} rows={3} placeholder="Text" className={"form-control b"} value={text}></textarea>
                       <div className="d-flex gap-4">
                           <select onChange={(e)=>setTourId(e.target.value)} className={"form-select b"}>
                               <option value={""} selected disabled>Tanlang</option>
                               {
                                   tour.tours.map((t,i)=><option value={t.id} key={t.id}>
                                       {t.title}
                                   </option>)
                               }
                           </select>
                           <button className={"buttonOrder2"}>Send</button>
                       </div>
                   </div>
               </form>
           </Rodal>
           <ToastContainer
               position="top-right"
               autoClose={3000}
               closeButton={true}
               hideProgressBar={false}
               pauseOnHover={false}
               theme="dark"
           />
       </div>
    );
};

export default MainContent;