import React from 'react';
import "./GroupTour.css"
import {useDispatch, useSelector} from "react-redux";
import {useNavigate} from "react-router-dom";
import {changeVisible} from "../../../store/slices/EnquirySlice";
import ApiCallSrc from "../../../apiCall/ApiCallSrc";

const GroupTours = () => {
    const tours = useSelector(state => state.tour.tours);
    const navigate = useNavigate();
    const dispatch = useDispatch();
    return (
        <div className="group-tours" id={"Group_Tours"}>
            <h2>Group Tours</h2>
            {tours.map(tour => (
                <div key={tour.id} className="tour-item">
                    <img src={ApiCallSrc()+tour.images[0].path} alt={tour.title} />
                    <div className="tour-details">
                        <h3>{tour.title}</h3>
                        <p>{tour.description}</p>
                        <p>{tour.description1}</p>
                        <p>{tour.details}</p>
                        <p>{tour.tourDay}   kun</p>
                    </div>
                    <div className="tour-actions">
                        <p className="price">{tour.price} $</p>
                        <button onClick={()=>{
                            navigate("/tour/"+tour.id)
                            dispatch(changeVisible())
                        }} className="order-btn">Order</button>
                        <button onClick={()=>{navigate("/tour/"+tour.id)}} className="detail-btn">Detail</button>
                    </div>
                </div>
            ))}

        </div>
    );
};

export default GroupTours;