import React, {useState} from 'react';
import './Header.css';
import logo from "./images/Logo.svg"
import telegram from "./images/telegram.svg"
import instagram from "./images/instagram.svg"
import watsap from "./images/Whatsaap.svg"
import facebook from "./images/Facebook.svg"
import fone from "./images/fone.svg"
import {useNavigate} from "react-router-dom";
import {changeLanguage} from "../../../store/slices/LanguageSlice";
import {useDispatch, useSelector} from "react-redux";
const Header = () => {
    const navigate = useNavigate();
    const [lang, setLang] = useState('')

    function changeLang(value) {
        setLang(value)
        localStorage.setItem("language",value)
        window.location.reload()
    }

    return (
       <div>
           {
               localStorage.getItem("language")==="LANG1"? <header className="header">
                   <div className="logo">
                       <img src={logo}/>
                   </div>
                   <nav className="nav">
                       <ul className={"gap-5 my-3"}>
                           <li onClick={()=>{navigate("/")}}><a href="#Group_Tours">Group Tours</a></li>
                           <li onClick={()=>{navigate("/")}}><a href="#About_Us">About Us</a></li>
                           <li onClick={()=>{navigate("/")}}><a href="#Contact_Us">Contact Us</a></li>
                       </ul>
                   </nav>
                   <div className="contact-info gap-2 " >
                       <div className="social-icons mx-5 ">
                           <a href="https://t.me/your_profile" target="_blank"><i className="fab fa-telegram mx-2">
                               <img src={telegram}/>
                           </i></a>
                           <a href="https://www.instagram.com/your_profile" target="_blank"><i className="fab fa-whatsapp mx-2">
                               <img src={instagram}/>
                           </i></a>
                           <a href="https://wa.me/your_phone_number" target="_blank"><i className="fab fa-facebook mx-2">
                               <img src={watsap}/>
                           </i></a>
                           <a href="https://www.facebook.com/your_profile" target="_blank"><i className="fab fa-instagram mx-2">
                               <img src={facebook}/>
                           </i></a>
                       </div>
                       <div>
                           <select onChange={(e)=>changeLang(e.target.value)} className={"form-select select-option  my-2"}>
                               <option selected={localStorage.getItem("language") === "LANG1"} value="LANG1">En</option>
                               <option selected={localStorage.getItem("language") === "LANG2"} value="LANG2">Gr</option>
                               <option selected={localStorage.getItem("language") === "LANG3"} value="LANG3">Fr</option>
                           </select>
                       </div>
                       <img src={fone}/>
                       <h7>+998901234567</h7>
                   </div>
               </header>
                   :localStorage.getItem("language")==="LANG2"? <header className="header">
                   <div className="logo">
                       <img src={logo}/>
                   </div>
                   <nav className="nav">
                       <ul className={"gap-5 my-3"}>
                           <li onClick={()=>{navigate("/")}}><a href="#Group_Tours">Gruppenreisen</a></li>
                           <li onClick={()=>{navigate("/")}}><a href="#About_Us">Über uns</a></li>
                           <li onClick={()=>{navigate("/")}}><a href="#Contact_Us">Kontaktieren Sie uns</a></li>
                       </ul>
                   </nav>
                   <div className="contact-info gap-2 " >
                       <div className="social-icons mx-5 ">
                           <a href="https://t.me/your_profile" target="_blank"><i className="fab fa-telegram mx-2">
                               <img src={telegram}/>
                           </i></a>
                           <a href="https://www.instagram.com/your_profile" target="_blank"><i className="fab fa-whatsapp mx-2">
                               <img src={instagram}/>
                           </i></a>
                           <a href="https://wa.me/your_phone_number" target="_blank"><i className="fab fa-facebook mx-2">
                               <img src={watsap}/>
                           </i></a>
                           <a href="https://www.facebook.com/your_profile" target="_blank"><i className="fab fa-instagram mx-2">
                               <img src={facebook}/>
                           </i></a>
                       </div>
                       <div>
                           <select onChange={(e)=>changeLang(e.target.value)} className={"form-select select-option  my-2"}>
                               <option selected={localStorage.getItem("language") === "LANG1"} value="LANG1">En</option>
                               <option selected={localStorage.getItem("language") === "LANG2"} value="LANG2">Gr</option>
                               <option selected={localStorage.getItem("language") === "LANG3"} value="LANG3">Fr</option>
                           </select>
                       </div>
                       <img src={fone}/>
                       <h7>+998901234567</h7>
                   </div>
               </header>
                   :localStorage.getItem("language")==="LANG3"? <header className="header">
                   <div className="logo">
                       <img src={logo}/>
                   </div>
                   <nav className="nav">
                       <ul className={"gap-5 my-3"}>
                           <li onClick={()=>{navigate("/")}}><a href="#Group_Tours">Visites de groupe</a></li>
                           <li onClick={()=>{navigate("/")}}><a href="#About_Us">à Propos de Nous</a></li>
                           <li onClick={()=>{navigate("/")}}><a href="#Contact_Us">Contactez-Nous</a></li>
                       </ul>
                   </nav>
                   <div className="contact-info gap-2 " >
                       <div className="social-icons mx-5 ">
                           <a href="https://t.me/your_profile" target="_blank"><i className="fab fa-telegram mx-2">
                               <img src={telegram}/>
                           </i></a>
                           <a href="https://www.instagram.com/your_profile" target="_blank"><i className="fab fa-whatsapp mx-2">
                               <img src={instagram}/>
                           </i></a>
                           <a href="https://wa.me/your_phone_number" target="_blank"><i className="fab fa-facebook mx-2">
                               <img src={watsap}/>
                           </i></a>
                           <a href="https://www.facebook.com/your_profile" target="_blank"><i className="fab fa-instagram mx-2">
                               <img src={facebook}/>
                           </i></a>
                       </div>
                       <div>
                           <select onChange={(e)=>changeLang(e.target.value)} className={"form-select select-option my-2"}>
                               <option selected={localStorage.getItem("language") === "LANG1"} value="LANG1">En</option>
                               <option selected={localStorage.getItem("language") === "LANG2"} value="LANG2">Gr</option>
                               <option selected={localStorage.getItem("language") === "LANG3"} value="LANG3">Fr</option>
                           </select>
                       </div>
                       <img src={fone}/>
                       <h7>+998901234567</h7>
                   </div>
               </header>:""
           }
       </div>
    );
};

export default Header;