import React from 'react';
import "./TourDays.css"
import img from './images/foto1.svg'
import icon from "./images/icon.svg"
import {useSelector} from "react-redux";
import ApiCallSrc from "../../../apiCall/ApiCallSrc";

function TourDays() {
    const tourDays = useSelector(state => state.tourDay.tourDays)
    const tour = useSelector(state => state.tour.tour);
    return (
        <div className={"divTourDay my-5"}>
            {
                tourDays.map((tourDay, i) =>
                    <div>
                        <div className={"dayDiv my-3"}>
                            <div>
                                <img className={"img10"}
                                     src={ApiCallSrc() + tourDay.images[0].path}/>
                            </div>
                            <div className={"divRight"}>
                                <div className={"d-flex"}>
                                    <h3 className={"h3DayTour"}>Day {i + 1}</h3>
                                    <h3 className={"h3DayTour2"}>{tourDay.title}</h3>
                                </div>
                                <div className={"my-3"}>
                                    <h5 className={"h3DayTour3"}>{tourDay.description}</h5>
                                </div>
                                <div>
                                    <h5 className={"h3DayTour4"}>
                                        <img className={"mx-2"} src={icon}/>
                                        City Palace Hotel or similar
                                    </h5>
                                </div>
                            </div>
                        </div>
                        <div className={"bottomHr"}></div>
                    </div>
                )
            }
            <div>
                <div className={"headerLorem"}>
                    <h3 className={"loremText"}>Additional Information</h3>
                </div>
                <div className={"footerLorem"}>
                    <h3 className={"loremText2"}>{tour.info}</h3>
                </div>
            </div>

        </div>
    );
}

export default TourDays;