import apiCall from "../../apiCall/ApiCall";
import ApiCallJson from "../../apiCall/ApiCallJson";
import apiCallJson from "../../apiCall/ApiCallJson";
import {toast} from "react-toastify";

const customMiddleware = (store) => (next) => (action) => {
    switch (action.type) {
        case "tour/addTour":
            apiCall("/tour/add", "POST", action.payload)
                .then((res) => {
                    fetchAllToursAndDispatch(store, next)
                    window.location.href = "/admin/tourDays/" + res.data.id
                })
                .catch((error) => handleApiError(error, next));
            break;
        case "tour/deleteTour":
            apiCall("/tour/delete?tourId=" + action.payload, "DELETE")
                .then(() => {
                    fetchAllToursAndDispatch(store, next)
                    toast.success("Successfully deleted")
                })
                .catch((error) => handleApiError(error, next));
            break;
        case "tour/updateTour":
            apiCall("/tour/edit", "put", action.payload)
                .then(() => fetchAllToursAndDispatch(store, next))
                .catch((error) => handleApiError(error, next));
            break;
        case "tour/updateTourWithImage":
            apiCall("/tour/edit/withImage", "put", action.payload)
                .then(() => fetchAllToursAndDispatch(store, next))
                .catch((error) => handleApiError(error, next));
            break;
        case "tour/updateTourWithVideo":
            apiCall("/tour/edit/withVideo", "put", action.payload)
                .then(() => fetchAllToursAndDispatch(store, next))
                .catch((error) => handleApiError(error, next));
            break;
        case "tour/updateTourWithVideoAndImage":
            apiCall("/tour/edit/withVideoAndImage", "put", action.payload)
                .then(() => fetchAllToursAndDispatch(store, next))
                .catch((error) => handleApiError(error, next));
            break;
        case "tour/getTour":
            fetchAllToursAndDispatch(store, next);
            break;
        case "tour/getTourAdmin":
            fetchAllToursAdminAndDispatch(store, next);
            break;
        case "tour/getTourById":
            fetchTourAndDispatch(store, next, action);
            break;


        case "tourDay/addTourDay":
            apiCall("/tourDay/add", "POST", action.payload)
                .then(() => {
                    fetchAllTourDaysAndDispatch(store, next, {payload: action.payload.tourId})
                    toast.success("Successfully added !")
                })
                .catch((error) => handleApiError(error, next));
            break;
        case "tourDay/getTourDay":
            fetchAllTourDaysAndDispatch(store, next, action);
            break;
        case "tourDay/deleteTourDay":
            apiCall("/tourDay/delete?tourDayId=" + action.payload.tourDayId, "DELETE")
                .then(() => fetchAllTourDaysAndDispatch(store, next, {payload: action.payload.tourId}))
                .catch((error) => handleApiError(error, next));
            break;
        case "tourDay/updateTourDay":
            apiCall("/tourDay/edit", "put", action.payload.currentTourDay)
                .then(() => fetchAllTourDaysAndDispatch(store, next, {payload: action.payload.id}))
                .catch((error) => handleApiError(error, next));
            break;
        case "tourDay/updateTourDayWithImage":
            apiCall("/tourDay/edit/withImage", "put", action.payload.currentTourDay)
                .then(() => fetchAllTourDaysAndDispatch(store, next, {payload: action.payload.id}))
                .catch((error) => handleApiError(error, next));
            break;


        case "comment/getComment":
            fetchAllCommentAndDispatch(store, next)
            break;
        case "comment/getCommentsByTourId":
            fetchCommentsByTourIdAndDispatch(store, next, action)
            break;
        case "comment/editComment":
            apiCall("/comments/change/visible?commentId=" + action.payload, "put")
                .then(() => fetchAllCommentAndDispatch(store, next))
                .catch((error) => handleApiError(error, next));
            break;
        case "comment/deleteComment":
            apiCall("/comments/delete?commentId=" + action.payload, "delete")
                .then(() => fetchAllCommentAndDispatch(store, next))
                .catch((error) => handleApiError(error, next));
            break;
        case "comment/addComment":
            ApiCallJson("/comments/add?tourId=" + action.payload.tourId, "post", action.payload.comment)
                .then(() => fetchCommentsByTourIdAndDispatch(store, next, {
                    payload: action.payload.tourId, type: "comment/getCommentsByTourId"
                }))
                .catch((error) => handleApiError(error, next));
            break;


        case "enquiry/getEnquiry":
            fetchAllEnquiriesAndDispatch(store, next)
            break;
        case "enquiry/addEnquiry":
            ApiCallJson("/enquiry/add", "post", action.payload).then(() => {
                toast.success("Successful !")
            }).catch(error => {
                handleApiError(error, next)
            })
            break;

        case "language/changeLanguage":
            apiCall("/user/lang?lang=" + action.payload.lang + "&userId=" + action.payload.userId, "put")
                .then(() => fetchAllAndDispatch(store, next, action))
                .catch((error) => handleApiError(error, next));
            break;
        case "enquiry/sendEnquiry":
            apiCallJson('/email/sendEmail', 'post', action.payload)
                .then(() => {
                    fetchAllEnquiriesAndDispatch(store, next);
                })
                .catch((error) => {
                    handleApiError(error, next);
                });
            break;


        case "email/getEmail":
            fetchAllEmailsAndDispatch(store, next)
            break;
        default:
            return next(action);
    }
};

const fetchAllToursAndDispatch = (store, next) => {
    apiCall("/tour/get/all", "GET")
        .then((response) => dispatchAction(store, next, {type: "tour/getTour", payload: response.data}))
        .catch((error) => handleApiError(error, next));
};
const fetchAllToursAdminAndDispatch = (store, next) => {
    apiCall("/tour/get/all/admin", "GET")
        .then((response) => dispatchAction(store, next, {type: "tour/getTour", payload: response.data}))
        .catch((error) => handleApiError(error, next));
};
const fetchAllCommentAndDispatch = (store, next) => {
    apiCall("/comments/visible?visible=false", "GET")
        .then((response) => dispatchAction(store, next, {type: "comment/getComment", payload: response.data}))
        .catch((error) => handleApiError(error, next));
};
const fetchCommentsByTourIdAndDispatch = (store, next, action) => {
    apiCall("/comments/get?tourId=" + action.payload, "GET")
        .then((response) => dispatchAction(store, next, {type: "comment/getComment", payload: response.data}))
        .catch((error) => handleApiError(error, next));
};

const fetchAllTourDaysAndDispatch = (store, next, action) => {
    apiCall("/tourDay/get?tourId=" + action.payload, "GET")
        .then((response) => dispatchAction(store, next, {type: "tourDay/getTourDay", payload: response.data}))
        .catch((error) => handleApiError(error, next));
};
const fetchTourAndDispatch = (store, next, action) => {
    apiCall("/tour/get?tourId=" + action.payload, "GET")
        .then((response) => dispatchAction(store, next, {type: "tour/getTourById", payload: response.data}))
        .catch((error) => handleApiError(error, next));
};
const fetchAllEnquiriesAndDispatch = (store, next) => {
    apiCall("/enquiry/getAll", "GET")
        .then((response) => dispatchAction(store, next, {type: "enquiry/getEnquiry", payload: response.data}))
        .catch((error) => handleApiError(error, next));
};

const fetchAllEmailsAndDispatch = (store, next, action) => {
    ApiCallJson("/email/get?id=" + action.payload, "get").then((response) => {
        dispatchAction(store, next, {type: "email/getEmail", payload: response.data})
    }).catch((error) => {
        handleApiError(error, next)
    })
};

const fetchAllAndDispatch = (store, next, action) => {
    dispatchAction(store, next, {type: "language/changeLanguage", payload: action.payload.lang});
    window.location.reload();
};

const dispatchAction = (store, next, action) => {
    next(action);
};

const handleApiError = (error, next) => {
    console.log(error.request.status)
    if (error.request.status === 403) {
        logout()
    }
    toast.error(error.response.data)
    next({type: "API_ERROR", payload: error.message});
};

const logout = () => {
    window.location.href = "/login"
    localStorage.removeItem("refresh_token")
    localStorage.removeItem("access_token")
    localStorage.removeItem("userId")
}

export default customMiddleware;
